* {
    box-sizing: border-box;
  }
  
  .all {
    margin: 0 auto;
  }
  
  div.title {
    display: flex;
    justify-content: center;
  }
  
  .title_img {
    width: 88px;
    height: 120px;
    flex-shrink: 0;
  }
  
  .title_h1 {
    color: #b09977;
    font-family: NanumMyeongjo;
    font-size: 48px;
    font-style: normal;
    font-weight: 800;
    line-height: 56px; /* 116.667% */
    letter-spacing: 5.76px;
  }
  
  .title_quote {
    color: #999;
    font-family: NanumMyeongjo;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px; /* 125% */
  }
  
  .login {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .login_info {
    width: 645px;
    height: 624px;
    flex-shrink: 0;
    border-radius: 16px;
    border: 1px solid rgba(176, 153, 119, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .label_id {
    display: flex;
    align-items: center;
    color: #999;
    font-family: NanumMyeongjo;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
  }
  
  #input_id {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
    border-radius: 16px;
    border: 1px solid #999;
  }
  
  .label_ps {
    display: flex;
    align-items: center;
    color: #999;
    font-family: NanumMyeongjo;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
  }
  
  #input_ps {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
    border-radius: 16px;
    border: 1px solid #999;
  }
  
  .button_login {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
  }
  
  .button_join {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
  }
  
  .login_info span {
    display: flex;
    justify-content: center;
  }
  
  .login_info span p {
    color: #999;
    font-family: NanumMyeongjo;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
  }
  
  .login_info span a {
    margin-left: 10px; /* 원하는 간격으로 조정 가능 */
    color: #999;
    font-family: NanumMyeongjo;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    text-decoration-line: underline;
  }
  