/* 요소 외부 폰트 모음 */
@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Nanum+Myeongjo&family=Noto+Sans+KR:wght@900&family=Noto+Serif+KR:wght@500&display=swap');


* {
    box-sizing: border-box;
  }
  
  .all {
    margin: 0 auto;
  }
  

  .nav {
    display:flex;
    justify-content:center;
    background-color: #0A559F;
    color : white;
    font-size:12px;
  }

  h3{
    height:60px;
    color:white;
    background-color:#062C7F;
    font-size:24px;
    text-align:center;
  }
  
  .label_id {
    display: flex;
    align-items: center;
    color: #999;
    font-family: 'Nanum Myeongjo', serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
  }

  
  .label_ps {
    display: flex;
    align-items: center;
    color: #999;
    font-family: 'Nanum Myeongjo', serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
  }

  .required{
    font-size:8px;
    color:red;
  }
  
  .button_login {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
  }
  
  .button_join {
    width: 540px;
    height: 68px;
    flex-shrink: 0;
  }

  .input_tab{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }